 {\n  flex: 0 0 40%;\n}\n.home-nav h3 {\n  color: rgba(55, 182, 134, 1);\n  margin-bottom: 15px;\n}\n.home-nav p {\n  color: rgba(0, 0, 0, 0.5);\n  font-size: calc(6px + 3vw / 2);\n}\n.search-bar {\n  display: flex;\n  flex-wrap: wrap;\n  align-items: center;\n}\n.search-bar p {\n  font-size: calc(5px + 3vw / 2);\n  font-weight: lighter;\n  margin-right: 10px;\n}\n.search-bar h2 {\n  font-size: calc(8px + 3vw / 2);\n  font-weight: lighter;\n  margin-right: 10px;\n}\n\n.posts h2 {\n  font-size: calc(8px + 3vw / 2);\n  margin-right: 10px;\n  font-weight: lighter;\n}\n.posts h3 {\n  font-size: calc(8px + 3vw / 2);\n  color: rgba(55, 182, 134, 1);\n  margin-right: 10px;\n  font-weight: lighter;\n}\n.posts p {\n  font-size: calc(5px + 3vw / 2);\n  font-weight: lighter;\n}\n\n.post {\n  flex-grow: 1;\n}\n.post h2 {\n  font-size: calc(8px + 3vw / 2);\n  margin-right: 10px;\n  text-align: left;\n  font-weight: lighter;\n}\n.post h3 {\n  font-size: calc(8px + 3vw / 2);\n  color: rgba(55, 182, 134, 1);\n  margin-right: 10px;\n  font-weight: lighter;\n}\n.post p {\n  font-size: calc(5px + 3vw / 2);\n  font-weight: lighter;\n}\n\n.post-content {\n  text-align: left;\n}\n\n.posts-content {\n  display: flex;\n  justify-content: space-between;\n}\n\n.posts-content form {\n  flex: 0 0 190px;\n}\n\n.posts-content label,\ninput,\ntextarea {\n  width: 100%;\n  font-size: calc(8px + 3vw / 2);\n  color: rgba(0, 0, 0, 0.5);\n  margin: 10px 0px 10px;\n  height: calc(25px + 3vw / 2);\n}\n\n.posts-content textarea {\n  padding: 10px;\n  border-right: 1px solid;\n  /* resize  : none; */\n  overflow: auto;\n  height: calc(100px + 3vw / 2);\n}\n\n.posts-content label + textarea {\n  margin-top: 0px;\n}\n\nform button {\n  display: block;\n  font-size: calc(8px + 3vw / 2);\n  color: rgba(0, 0, 0, 0.75);\n  margin: 10px 0px 10px;\n  background: white;\n  box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.2);\n  border: 2px solid rgb(51, 51, 51);\n  border-radius: 5px;\n  padding: 5px;\n  cursor: pointer;\n  text-align: center;\n  height: calc(25px + 3vw / 2);\n  width: calc(120px + 3vw / 2);\n}\n\nform button:after {\n  content: '';\n}\n\nform button:hover,\nbutton:focus {\n  outline: none;\n  background: rgba(55, 182, 134, 1);\n  color: #fff;\n}\n\nform input:focus,\ntextarea:focus {\n  background: rgba(0, 0, 0, 0.1);\n  border-radius: 5px;\n}\n\n.posts-content .posts-list {\n  flex-grow: 1;\n}\n\n.posts-list {\n  display: block;\n}\n\n.popular p {\n  font-size: calc(5px + 3vw / 2);\n  font-weight: lighter;\n}\n.popular h2 {\n  font-size: calc(8px + 3vw / 2);\n  font-weight: lighter;\n}\n\n.popular-labs {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  grid-template-rows: 50% 50%;\n  justify-content: space-around;\n  background-color: rgba(253, 253, 253, 1);\n  margin-bottom: 10px;\n}\n.popular-labs .card.top {\n  grid-row: 1 / span 2;\n  grid-column: 1 / span 1;\n}\n\n.popular-labs .card.next {\n  grid-row: 1 / span 1;\n  grid-column: 2 / span 2;\n}\n\n.popular-labs h3 {\n  color: rgba(55, 182, 134, 1);\n  margin-bottom: 15px;\n  font-size: calc(6px + 3vw / 2);\n}\n.popular-labs p {\n  color: rgba(0, 0, 0, 0.5);\n  font-size: calc(4px + 3vw / 2);\n}\n\n.footer {\n  padding: calc(10px + 3vw / 2) 0px;\n  color: white;\n  background-color: rgba(80, 159, 183, 1);\n}\n\n.footer p {\n  color: rgba(255, 255, 255, 1);\n  font-size: calc(1px + 3vw / 2);\n}\n\n.card {\n  background: white;\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);\n  padding: 20px;\n  margin: 10px;\n  text-align: center;\n  display: flex;\n  justify-content: space-around;\n  height: fit-content;\n}\n\n.card a {\n  font-size: calc(8px + 3vw / 2);\n  color: rgba(0, 0, 0, 0.75);\n  margin: 10px 0px 10px;\n  background: white;\n  box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.2);\n  border: 2px solid rgb(51, 51, 51);\n  border-radius: 5px;\n  padding: 5px;\n  cursor: pointer;\n  text-align: center;\n  text-decoration: none;\n  height: calc(25px + 3vw / 2);\n  width: calc(120px + 3vw / 2);\n}\n\n.card a:after {\n  content: '';\n}\n\n.card a:hover,\na:focus {\n  outline: none;\n  background: rgba(55, 182, 134, 1);\n  color: #fff;\n}\n\n@media (max-width: 992px) {\n  .container {\n    width: calc(700px + 3vw / 2);\n  }\n}\n\n@media (max-width: 768px) {\n  .container {\n    width: calc(460px + 3vw / 2);\n  }\n}\n\n@media (max-width: 480px) {\n  .container {\n    width: calc(300px + 3vw / 2);\n  }\n}\n\n@media (max-width: 768px) {\n  .posts-content {\n    display: block;\n  }\n  .posts-list {\n    display: block;\n  }\n  .posts-list .card {\n    flex: unset;\n  }\n}\n\n@media (max-width: 768px) {\n  .home-nav {\n    display: block;\n  }\n  .home-nav .card {\n    flex: unset;\n  }\n}\n\n@media (max-width: 480px) {\n  .home-nav .card {\n    flex: unset;\n  }\n}\n\n@media (max-width: 480px) {\n  .popular-labs {\n    grid-template-columns: unset;\n    grid-template-rows: unset;\n  }\n  .popular-labs .card.top {\n    grid-row: unset;\n    grid-column: unset;\n  }\n  .popular-labs .card.next {\n    grid-row: unset;\n    grid-column: unset;\n  }\n}\n"],"sourceRoot":""}]);
